@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
	margin: 0;
	font-family: 'Roboto', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.cases-types {
    border-radius: 14px;
    height:15vh;
}

.cases-types span {
    color: white;
}

.home-graph {
    height: 26vh;
}
.cases-type {
    border-radius: 12px;
    font-size: 20px;
    cursor: pointer;
}

.cases-type:hover {
    box-shadow: 0 0 6px 6px #0004;
}
.dist-graph {
	height: 21vh;
}
.vaccine-graph {
	height: 35vh;
}
 /* unvisited link */
a:link, a:visited, a:active {
	color: gold;
}

/* mouse over link */
a:hover {
	color: lime;
} 

.about {
	background: radial-gradient(circle, rgb(65, 102, 202) -60%, rgb(0, 0, 0) 75%);
	border: 10px double orangered;
	border-radius: 16px;
	color: #ffff4d;
	text-shadow: 2px 2px 8px black;
	padding: 10vh;
	text-align: center;
	-webkit-transform: translateY(15%);
	        transform: translateY(15%);
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 0 auto;
}

.list {
	text-align: left;
	width: -webkit-max-content;
	width: max-content;
	margin: 0 auto;
}
.animation-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.covid-19 { 
	width: 200px;
	margin-top: -100px;
	-webkit-animation-name: float;
	        animation-name: float;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

.shadow {
	width: 100px;
	height: 10px;
	background:slategrey;
	border-radius: 70%;
	opacity: 1;
	-webkit-filter: blur(3px);
	        filter: blur(3px);
	-webkit-animation-name: animate-shadow;
	        animation-name: animate-shadow;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

g.evelope {
	-webkit-animation-name: rotate;
	        animation-name: rotate;
	-webkit-transform-origin: 100px 105px;
	        transform-origin: 100px 105px;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

ellipse.left-eyeball,
ellipse.right-eyeball {
	-webkit-animation-name: moveEyeBalls;
	        animation-name: moveEyeBalls;
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
	-webkit-animation-direction: normal;
	        animation-direction: normal;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

.left-eyebrows,
.right-eyebrows {
	-webkit-animation-name: moveEyebrows;
	        animation-name: moveEyebrows;
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

.mouth {
	-webkit-animation-name: moveMouth;
	        animation-name: moveMouth;
	-webkit-transform-origin: 50% 50%;
	        transform-origin: 50% 50%;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

@-webkit-keyframes moveMouth {
	
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	} 100% {
		-webkit-transform: rotate(12deg);
		        transform: rotate(12deg);
	}

}

@keyframes moveMouth {
	
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	} 100% {
		-webkit-transform: rotate(12deg);
		        transform: rotate(12deg);
	}

}

@-webkit-keyframes moveEyebrows {
	
	0% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	} 100% {
		-webkit-transform: translateY(-1px);
		        transform: translateY(-1px);
	}

}

@keyframes moveEyebrows {
	
	0% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	} 100% {
		-webkit-transform: translateY(-1px);
		        transform: translateY(-1px);
	}

}

@-webkit-keyframes moveEyeBalls {
	
	0% {
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	} 25% {
		-webkit-transform: translateX(2px);
		        transform: translateX(2px);
	} 50%{
		-webkit-transform: translateX(-2px);
		        transform: translateX(-2px);
	} 75%{
		-webkit-transform: translateX(-2px);
		        transform: translateX(-2px);
	} 100% {
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	}

}

@keyframes moveEyeBalls {
	
	0% {
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	} 25% {
		-webkit-transform: translateX(2px);
		        transform: translateX(2px);
	} 50%{
		-webkit-transform: translateX(-2px);
		        transform: translateX(-2px);
	} 75%{
		-webkit-transform: translateX(-2px);
		        transform: translateX(-2px);
	} 100% {
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	}

}

@-webkit-keyframes float {
	
	0% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	} 100% {
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
	}
}

@keyframes float {
	
	0% {
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
	} 100% {
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
	}
}

@-webkit-keyframes animate-shadow {
	
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	} 100% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
}

@keyframes animate-shadow {
	
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	} 100% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
}

@-webkit-keyframes rotate {
	
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	} 50% {
		-webkit-transform: rotate(4deg);
		        transform: rotate(4deg);
	} 100% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	
}

@keyframes rotate {
	
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	} 50% {
		-webkit-transform: rotate(4deg);
		        transform: rotate(4deg);
	} 100% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	
}
.loader {
	height: 60vh;
	pointer-events: none;
	-webkit-animation: loader-spin infinite 20s linear;
	        animation: loader-spin infinite 20s linear;
}

.wrapper {
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

@-webkit-keyframes loader-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes loader-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

